import {
  prefixZero
} from './NumbePipes'

/**
 * 
 *
 * @export
 * @param {*} value 
 * @param {*} Format 
 * @returns
 */
export function formatDate (value, Format) {
  value = new Date(value * 1000)
  var padLeftZero = function (str) {
    return ('00' + str).substr(str.length)
  }
  if (/(y+)/.test(Format)) {
    Format = Format.replace(RegExp.$1, (value.getFullYear() + '').substr(4 - RegExp.$1.length))
  }
  let o = {
    'M+': value.getMonth() + 1,
    'd+': value.getDate(),
    'h+': value.getHours(),
    'm+': value.getMinutes(),
    's+': value.getSeconds()
  }
  for (let k in o) {
    if (new RegExp(`(${k})`).test(Format)) {
      let str = o[k] + ''
      Format = Format.replace(RegExp.$1, RegExp.$1.length === 1 ? str : padLeftZero(str))
    }
  }
  return Format
}

// xxx
export function formatSeconds (value, format) {
  value = Number(value)
  var day = Math.floor(value / (24 * 3600)) // Math.floor()
  var hour = Math.floor((value - day * 24 * 3600) / 3600)
  var minute = Math.floor((value - day * 24 * 3600 - hour * 3600) / 60)
  // var second = value - day * 24 * 3600 - hour * 3600 - minute * 60
  // switch(format) {
  //   case 'dhms':
  //     return day + ""  + hour + "" + minute + "" + second + "";
  //   case 'dhms':
  //     return day + ""  + hour + "" + minute + "" + second + "";
  // }
  return (day > 0 ? day + '' : '') + prefixZero(hour, 2) + '' + prefixZero(minute, 2) + '' //  + second + ""
}



