/**
 * 
 *
 * @export
 * @param {*} value 
 * @param {*} decimals 
 * @param {*} unit $
 * @returns
 */
export function currency (value, decimals, unit) {
  let result = parseFloat(value)
  if (isNaN(result)) {
    return NaN
  }
  decimals = decimals || 2
  if (unit === null || unit === undefined) {
    unit = ''
  }
  let valueStr = String(value).split('.')
  let decimalsPart = 0
  if (valueStr.length > 1) {
    decimalsPart = Number(valueStr[1])
  }

  result = decimalsPart > 0 ? result.toFixed(decimals) : parseInt(result)
  return unit + (result || 0)
};

/**
* 0
* @param value 
* @param length 
*/
export function prefixZero (value, length) {
  return (Array(length).join(0) + value).slice(-length)
}



